.item {
  width: 90px;
  height: 80px;

  a {
    text-decoration: none;
    color: #181818;
  }

  a.activeLink h3 {
    transform: scale(1.05, 1.05);
    color: #0275b1;
  }

  img {
    margin: 0 auto;
    padding-top: 18px;
    display: block;
    max-width: 24px;
    max-height: 24px;
  }
  h3 {
    padding-top: 10px;
    text-align: center;
    font-size: 12px;
    text-transform: uppercase;
    color: #181818;

    &:hover {
        transform: scale(1.05, 1.05);
        color: #0275b1;
      }
  }
}